<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from '../在线考试列表/components/TopButtons'
    import {getUserExamExcelList, deleteExamExcel} from '@/api/examExcel'
    import tableComponent from '@/components/table'

    export default {
        components: {
            TopButtons, tableComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '上传人',
                        dataIndex: 'user_name',
                        key: 'user_name',
                    },
                    {
                        title: '分类名称',
                        dataIndex: 'category_name',
                        key: 'category_name',
                    },
                    {
                        title: '题目数量',
                        dataIndex: 'question_count',
                        key: 'question_count',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            edit: {visible: true, disable: false,},
                            delete: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getUserExamExcelList,
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleEdit(id) {
                this.$router.push({name: '在线考试题库编辑题目', params: {id: id}})
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'create':
                        this.handleCreate();
                        break;
                    case 'edit':
                        this.handleEdit(row.id);
                        break;
                    case 'delete':
                        deleteExamExcel(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>
